import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  InputNumber,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
} from "antd";
import { room_update } from "../../../api/room";
import { area_get } from "../../../api/area";
const { Option } = Select;
const { TextArea } = Input;
const UpdateRoom = (props) => {
  const { onCancel, visible, onOk, info } = props;
  const [form] = Form.useForm();
  const [address, set_address] = useState([]);
  //提交
  const submit = async () => {
    // console.log(form.getFieldsValue());
    try {
      form.validateFields().then(async () => {
        // await role_create({
        //   ...form.getFieldsValue(),
        // });
        await room_update({
          ...form.getFieldsValue(),
        });
        form.resetFields();
        message.success("修改成功");
        onOk();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  };

  const on_cancel = () => {
    form.resetFields();
    onCancel();
  };
  //获取地区
  const regions_get_h = async () => {
    try {
      let data = await area_get({});
      // console.log(data);
      if(data){
        let addr = data?.map((item) => ({
          title: item.region,
          value: item.appid,
        }));
        set_address(addr);
      }
      
    } catch (error) {
      console.error(error);
    }
  };
  useEffect(() => {
    if (visible) {
      form.setFieldsValue({
        ...info,
      });
      regions_get_h();
    }
  }, [visible]);
  return (
    <Modal
      title="修改房间"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="房间id"
          name="rid"
          rules={[{ required: true, message: "请输入房间id" }]}
        >
          <InputNumber disabled  placeholder="请输入房间id" />
        </Form.Item>
        <Form.Item
          label="资源类型"
          name="asset_type"
          rules={[{ required: true, message: "请选择资源类型" }]}
        >
          <Select placeholder="请选择资源类型">
            <Option value={1}>1-Cash</Option>
            <Option value={99}>99-练习币</Option>
          </Select>
        </Form.Item>
        <Form.Item
          label="玩法类型"
          name="room_type"
          rules={[{ required: true, message: "请选择玩法类型" }]}
        >
          <Select placeholder="请选择玩法类型">
            <Option value={1}>13 Points</Option>
            <Option value={2}>10 Card</Option>
            <Option value={3}>Pool</Option>
          </Select>
        </Form.Item>
        <Form.Item
          label="pool场分数限制"
          name="score_limit"
          rules={[{ required: true, message: "请选择pool场分数限制" }]}
        >
          {/* <Select placeholder="请选择分数限制">
            <Option value={1}>N/A</Option>
            <Option value={1}>101</Option>
            <Option value={1}>201</Option>
          </Select> */}
          <InputNumber  placeholder="请输入pool场分数限制" />
        </Form.Item>
        <Form.Item
          label="最大玩家人数"
          name="max_player"
          rules={[{ required: true, message: "请输入最大玩家人数" }]}
        >
          <Select placeholder="请选择最大玩家人数">
            <Option value={2}>2</Option>
            <Option value={6}>6</Option>
          </Select>
        </Form.Item>
        <Form.Item
          label="底注(分)"
          name="ante"
          rules={[{ required: true, message: "请输入底注" }]}
        >
          <InputNumber  placeholder="请输入底注" />
        </Form.Item>
        <Form.Item
          label="玩家最低带入"
          name="min_wealth"
          rules={[{ required: true, message: "请输入玩家最低带入" }]}
        >
          <InputNumber  placeholder="请输入玩家最低带入" />
        </Form.Item>
        <Form.Item
          label="玩家最高带入"
          name="max_wealth"
          rules={[{ required: true, message: "请输入玩家最高带入" }]}
        >
          <InputNumber  placeholder="请输入玩家最高带入" />
        </Form.Item>
        <Form.Item
          label="覆盖区域"
          name="regions"
          rules={[{ required: true, message: "请选择覆盖区域" }]}
        >
          <Select placeholder="请选择覆盖区域">
            {address.map((item) => (
              <Option key={item.value} value={item.value}>
                {item.title}
              </Option>
            ))}
          </Select>
        </Form.Item>
        <Form.Item
          label="初始弃牌分数"
          name="virgin_drop_score"
          rules={[{ required: true, message: "请输入初始弃牌分数" }]}
        >
          <InputNumber  placeholder="请输入初始弃牌分数" />
        </Form.Item>
        <Form.Item
          label="中途弃牌分数"
          name="drop_score"
          rules={[{ required: true, message: "请输入中途弃牌分数" }]}
        >
          <InputNumber  placeholder="请输入中途弃牌分数" />
        </Form.Item>
        <Form.Item
          label="扣分下限"
          name="min_score"
          rules={[{ required: true, message: "请输入扣分下限" }]}
        >
          <InputNumber  placeholder="请输入请输入扣分下限" />
        </Form.Item>
        <Form.Item
          label="扣分上限"
          name="max_score"
          rules={[{ required: true, message: "请输入扣分上限" }]}
        >
          <InputNumber  placeholder="请输入扣分上限" />
        </Form.Item>
        <Form.Item
          label="最大超时弃牌次数"
          name="max_timeout_count"
          rules={[{ required: true, message: "请输入最大超时弃牌次数" }]}
        >
          <InputNumber  placeholder="请输入最大超时弃牌次数" />
        </Form.Item>
        <Form.Item
          label="牌副数"
          name="decks"
          rules={[{ required: true, message: "请输入牌副数" }]}
        >
          <InputNumber  placeholder="请输入牌副数" />
        </Form.Item>
        <Form.Item
          label="每副牌使用Joker数量"
          name="jokers"
          rules={[{ required: true, message: "请输入每副牌使用Joker数量" }]}
        >
          <InputNumber  placeholder="请输入每副牌使用Joker数量" />
        </Form.Item>
        <Form.Item
          label="手牌张数"
          name="hands"
          rules={[{ required: true, message: "请输入手牌张数" }]}
        >
          <InputNumber  placeholder="请输入手牌张数" />
        </Form.Item>
        <Form.Item
          label="状态"
          name="status"
          rules={[{ required: true, message: "请选择状态" }]}
        >
          <Select placeholder="请选择状态">
            <Option value={0}>开启</Option>
            <Option value={1}>关闭</Option>
          </Select>
        </Form.Item>
        <Form.Item
          label="玩法类型描述"
          name="game_type"
          rules={[{ required: true, message: "请输入玩法类型描述" }]}
        >
          <TextArea placeholder="请输入玩法类型描述" />
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(UpdateRoom);
